* {
  width: 21cm;
  height: 29.7cm;
}
body {
  margin: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.7)),
    url("https://images.unsplash.com/photo-1542546068979-b6affb46ea8f?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1868&q=80");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.main {
  display: flex;
  flex-direction: row;
  color: white;
}

.side {
  background-color: rgba(0, 0, 0, 0.5);
  height: 15cm;
  width: 7cm;
  margin: 1.5cm 0 0 0.5cm;
  display: flex;
  flex-direction: column;
  place-items: center;
  text-align: center;
  border-radius: 15px;
  font-size: 0.7em;
}

.side p,
h1 {
  display: flex;
  flex-direction: column;
  padding: 0;
  width: 7cm;
  height: 20px;
}
.side p {
  font-size: 1.5em;
}

.side img {
  margin-top: 50px;
  box-shadow: 0px 5px 24px 3px rgba(0, 0, 0, 0.88);
  width: 150px;
  height: 150px;
  border-radius: 50%;
}

.side ul {
  margin: 0;
  padding: 0;
  height: 280px;
  width: 7cm;
  list-style-type: none;
}
.side li {
  width: 7cm;
  height: 60px;
  place-items: center;
  padding: 0;
}

.side li p {
  font-size: 1.3em;
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 5px 0 0 0;
}

.right-side {
  padding: 0;
  width: 12.5cm;
  display: flex;
  flex-direction: column;
  place-items: center;
  text-align: center;
}
.right-side h1 {
  margin: 1.5cm 0 0 0.5cm;
  width: 10cm;
  height: 1cm;
  text-align: center;
  border-bottom: 2px solid rgb(90, 89, 88, 0.5);
}

.right-side p {
  margin: 0.4cm 0 0 0.5cm;
  width: 10cm;
  height: auto;
}

.projects {
  display: flex;
  flex-direction: column;
  place-items: center;
  text-align: center;
  position: absolute;
  left: 0;
  top: 90%;
  left: 0;
  height: 10cm;
}
.projects h1 {
  width: 15cm;
  height: 1cm;
  margin: 1cm 0 0 0.5cm;
}
.projects p {
  margin: 0.4cm 0 0 0.5cm;
  width: 15cm;
  height: auto;
}
